<!-- todo -->
<template>
    <div class="todo-index-box">
        <Add :addItem="addItem"/>
        <List :list = "todos" :delItem="delItem"/>
        <Footer :list="todos" :selectAll="selectAll" :delCompleted="delCompleted" v-if="todos.length>0"/>
    </div>
</template>

<script>
import Add from './Add'
import List from './List'
import Footer from './Footer'
export default {
data () {
    return {
        // todos:[
        //     {title:'one',complete:false},
        //     {title:'two',complete:true},
        // ],
        todos:JSON.parse(window.localStorage.getItem('todos_key') || '[]')
    }
},

watch:{
    todos:{
        deep:true,
        handler:function(value){  // 存newValue
            window.localStorage.setItem('todos_key',JSON.stringify(value))
        }
    },
},

props: {},

components: {
    Add,
    List,
    Footer
},

created () {},

computed: {},

mounted (){},

methods: {
    addItem(msg){
        this.todos.unshift(msg)
    },
    delItem(index){
        this.todos.splice(index,1)
    },
    selectAll(isCheck){
        this.todos.forEach(item => item.complete = isCheck)
    },
    delCompleted(){
        console.log('delCompleted---')
        this.todos = this.todos.filter( item => !item.complete)  // 删除为check为true的选项
    }
}
}

</script>
<style>
.todo-index-box{
    margin: 20px auto;
    padding: 10px;
    box-sizing: border-box;
    width: 500px;
    border: 1px solid #000;
    border-radius: 10px;
    box-shadow: gray 0 0 10px;
}
</style>
